.TodoItem {
    background-color: #FAFAFA;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    margin-left: 12px;
    margin-right: 12px;
    box-shadow: 0px 0px 16px 4px rgba(32, 35, 41, 0.50);
    border-radius: 36px;
}

.TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}

.TodoItem-p--complete {
    text-decoration: line-through;
}

.Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
}

.Icon-check {
    position: absolute;
    left: 12px;
}

.Icon-check--active {
    color: #4CAF50;
}

.Icon-delete {
    position: absolute;
    margin-right: 8px;
    right: 0;
}

.Icon-delete:hover {
    color: #FE3535;
}